  margin: 0 0 0 19px;
}
.advantage__goto::after {
  content: "";
  background-image: url("../img/icon_arrow.svg");
  background-position-x: -9px;
  background-repeat: no-repeat;
  height: 12px;
  width: 32px;
  margin: 0 0 0 17px;
  transition: background 0.1s ease;
