  max-width: 550px;
  min-width: 320px;
  box-sizing: border-box;
  padding: 0 20px 0 20px;
  height: 150px;
}

.comment-form__text {
  box-sizing: border-box;
  width: 100%;
  height: 150px;
  margin: 0;
}

.textarea-form {
  font-family: "oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  color: #444444;
  font-size: 16px;
  padding: 6px 0 0 15px;
  resize: none;
}
.textarea-form::placeholder {
  color: #444444;
}

.additional-form {
  background-image: linear-gradient(#68b738, #68b738);
  background-size: 100% 2px;
  background-repeat: no-repeat;
  background-position: left 18px;
}

.additional-form__container {
  max-width: 550px;
  min-width: 320px;
  box-sizing: border-box;
  margin: 0 auto;
}

.additional-form__title {
  margin: 0 0 34px;
  width: 45vw;
  padding-left: 20px;
  padding-right: 19px;
  background-color: #ffffff;
}

.additional-form__list {
  margin-left: 20px;
  list-style: none;
  padding-left: 0;
}

.additional-list__item:not(:last-of-type) {
  margin-bottom: 12px;
}

.check-form {
  position: relative;
  height: 30px;
}

.check-form__name {
  padding-left: 46px;
  text-transform: uppercase;
  font-size: 16px;
  font-family: "oswald", "Arial", sans-serif;
  font-weight: 400;
  cursor: pointer;
  display: inline-block;
  line-height: 30px;
}
.check-form__name:hover::before {
  border-color: #bababa;
}
.check-form__name:active {
  opacity: 0.3;
}

.check-form__name::before {
  content: "";
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.check-form__name::after {
  content: "";
  background-image: url("../../img/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
  width: 30px;
  height: 30px;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.check-form__box:checked + .check-form__name::after {
  display: block;
}

.submit {
  max-width: 550px;
  min-width: 320px;
  box-sizing: border-box;
}

.submit__container {
  margin: 0 20px;
}

.submit__button {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 21px;
}

.submit__legend {
  text-align: center;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
}

.main-form__cat {
  margin: 0 auto 23px;
}

.main-form__owner {
  margin-bottom: 20px;
}

.main-form__comment {
  margin-bottom: 20px;
}

.main-form__additional {
  margin-bottom: 35px;
}

.main-form__submit {
  margin: 0 auto 32px;
}

.cat-change {
  position: relative;
}

.cat-change__slider {
  margin: 0 auto 14px;
}

.cat-change__controller {
  margin: 0 auto;
  display: flex;
  align-items: center;
  width: 208px;
}

.cat-change__btn {
  font-family: "oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  border: none;
  cursor: pointer;
  color: #000000;
  text-transform: uppercase;
  width: 49px;
}
.cat-change__btn:hover {
  background-color: transparent;
}

.cat-change__btn--start {
  margin-right: 15px;
}

.cat-change__controller-slider {
  height: 14px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  background-color: #ffffff;
  border-radius: 6px;
  width: 100%;
  position: relative;
}

.cat-change__controller-handle {
  height: 6px;
  border: 1px solid #68b738;
  border-radius: 6px;
  width: 36px;
  position: absolute;
  top: 3px;
  left: 6px;
  background-color: #68b738;
  transition: all 2s;
  padding: 0;
}

.cat-change__controller-handle--before {
  left: 5px;
}

.cat-change__controller-handle--after {
  left: 43px;
}

.cat-change__btn--end {
  margin-left: 14px;
}

@media (min-width: 768px) {
  .cat-change {
    background: #eaeaea;
  }

  .cat-change::before {
    content: "";
    display: block;
    background: #ffffff;
    height: 172px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .cat-change__slider {
    margin: 0 auto 26px;
  }

  .cat-change__controller {
    width: 592px;
    margin: 0 auto;
    height: 40px;
    box-sizing: border-box;
    padding: 0;
  }

  .cat-change__btn {
    font-size: 20px;
    line-height: 30px;
    border-bottom: 1px dotted #cdcdcd;
    padding: 0;
  }

  .cat-change__btn--start {
    margin-left: 14px;
    margin-right: 23px;
  }

  .cat-change__controller-slider {
    height: 6px;
    border: 1px solid #dcdcdc;
    background-color: #dcdcdc;
    border-radius: 6px;
  }

  .cat-change__controller-handle {
    height: 34px;
    width: 34px;
    border-radius: 50%;
    background-color: #ffffff;
    border: 1px solid #cdcdcd;
    top: -15px;
    left: 196px;
  }

  .cat-change__controller-handle::after {
    content: "";
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background-color: #68b738;
    position: absolute;
    top: 12px;
    left: 12px;
  }

  .cat-change__controller-handle--before {
    left: -15px;
  }

  .cat-change__controller-handle--after {
    left: 405px;
  }

  .cat-change__controller-handle--middle {
    left: 200px;
  }

  .cat-change__btn--end {
    margin-left: 23px;
    margin-right: 9px;
  }
}
@media (min-width: 1440px) {
  .cat-change__controller {
    margin: 0 auto;
  }
}
